export enum EmployeeRole {
  CASHER = "CASHER",
  SELLER = "SELLER",
  SUPERVISOR = "SUPERVISOR",
}

export interface Employee {
  id: string;

  externalId: string;

  role: EmployeeRole;

  name: string;

  middleName: string;

  lastName: string;

  motherLastName: string;

  company: string;

  creationDate: number;

  lastUpdateDate: number;
}
